.header__logo {
  width: 142px;
  height: 33px;
}

@media screen and (max-width:1152px) {
  .header__logo {
    width: 104px;
    height: 24px;
    margin-left: 30px;
  }
}

@media screen and (max-width:500px) {
  .header__logo {
    margin: 0;
    position: absolute;
    left: 30px;
  }
}