.profile__caption {
  max-width: 336px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width:1152px) {
  .profile__caption {
    max-width: 282px;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }
}